:root {
  --res-bg: #f4f2ee;
  --res-white: #ffffff;
  --res-white-soft: #f8f8f8;
  --res-white-mute: #f2f2f2;

  --res-black: #181818;
  --res-black-soft: #222222;
  --res-black-mute: #282828;

  --res-accented-text: #c02f1d; //#AD2A1A;//#9A2617;//#ff6f00;
  --res-accented-text-hover: rgba(192, 47, 29, 0.05);
  --res-linking-text: #2196f3;
  --res-linking-text-hover: rgba(33, 150, 243, 0.1);
  --res-gray-1: #757575;

  --res-divider-light-1: rgba(60, 60, 60, 0.29);
  --res-divider-light-2: rgba(60, 60, 60, 0.12);
  --res-divider-dark-1: rgba(84, 84, 84, 0.65);
  --res-divider-dark-2: rgba(84, 84, 84, 0.48);

  --res-text-light-1: var(--res-black);
  --res-text-light-2: rgba(60, 60, 60, 0.66);
  --res-text-dark-1: var(--res-white);
  --res-text-dark-2: rgba(235, 235, 235, 0.64);
}

:root {
  // --color-background: var(--res-white);
  --color-background: var(--res-bg);
  --color-background-soft: var(--res-white-soft);
  --color-background-mute: var(--res-white-mute);

  --color-border: var(--res-divider-light-2);
  --color-border-hover: var(--res-divider-light-1);

  --color-heading: var(--res-text-light-1);
  --color-text: var(--res-text-light-1);

  --section-gap: 160px;

  --ds-background: #f4f1de;
  --ds-accent: #e07a5f;
  --ds-header: #3D405B;
  --ds-text: #f4f1de;
  --ds-green: #81B29A;
  // --ds-background: #f4f1de;
  // --ds-accent: #e07a5f;
  // --ds-text: #3D405B;
  // --ds-green: #81B29A;
  // --ds-muted: #f2cc8f;

  --ds-dark-background: #65667d;
  --ds-dark-header: #3D405B;
}

